<template>
	<view class="personal">
		<view class="personal_option">
			<view class="personal_option_item">
				<view class="title">收票人姓名</view>
				<input v-model="info.invoice_head" class="content" placeholder="请输入名称(必填)" clearable="false" />
			</view>
			<view class="personal_option_item">
				<view class="title">收票人电话</view>
				<input v-model="info.invoice_phone" class="content" placeholder="(选填)" clearable="false" />
			</view>
			<view class="personal_option_item">
				<view class="title">收票人邮箱</view>
				<input v-model="info.email" class="content" placeholder="(选填)" clearable="false" />
			</view>
		</view>
		<view class="preservation h_auto">
			<view class="btn" @click="saveBtn">保存</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				info: {
					invoice_type: 1,
					invoice_head: '',
					email: '',
					invoice_phone: '',
				}
			}
		},
		onLoad(option) {
			this.id = option.id
			if (this.id != undefined) {
				this.$u.post('/Ucenter/getInvoiceDetail', {
					id: this.id
				}).then(res => {
					res.data.invoice_type = 1
					this.info = res.data
				})
			}
		},
		methods: {
			saveBtn() {
				this.$u.throttle(this.toInvoice, 1000)
			},
			toInvoice() {
				if (this.info.id == undefined) {
					if ((this.$u.test.isEmpty(this.info.invoice_head)) == true) {
						uni.showToast({
							title: '收票人姓名不能为空',
							duration: 2000,
							icon: 'none'
						})
					} else if (this.$u.test.rangeLength(this.info.invoice_head, [2, 5]) == false) {
						uni.showToast({
							title: '请输入正确的收票人',
							duration: 2000,
							icon: 'none'
						})
					} else {
						this.$u.post('/Ucenter/addInvoice', this.info).then(res => {
							if (res.error === 0) {
								uni.showToast({
									title: '添加成功',
									duration: 2000,
									icon: 'success'
								});
								setTimeout(() => {
									uni.navigateBack({
										delta: 1
									})
								}, 1500)
							} else {
								uni.showToast({
									title: res.msg,
									icon: 'none'
								})
							}
						})
					}
				} else {
					this.$u.post('/ucenter/editInvoice', this.info).then(res => {
						// res为服务端返回的数据
						if (res.error == 0) {
							uni.showToast({
								title: '修改成功',
								duration: 2000,
								icon: 'success'
							});
							setTimeout(() => {
								uni.navigateBack({
									delta: 1
								})
							}, 1500)
						}
					})
				}
			}
		},
	}
</script>

<style lang="less" scoped>
	page {
		background-color: #F3F4F5;
	}

	.personal {
		background-color: #F3F4F5;
		padding: 12px 0;
		height: 100vh;

		.personal_option {
			width: 100%;
			background-color: #FFFFFF;

			.personal_option_item:last-child {
				border: none;
			}

			.personal_option_item {
				border-bottom: 1px solid rgba(0, 0, 0, 0.1);
				box-sizing: border-box;
				display: flex;
				align-items: center;

				.title {
					padding: 12px 0;
					color: #303133;
					font-size: 14px;
					width: 104px;
					margin-left: 20px;
					box-sizing: border-box;
				}

				.content {
					flex: 1;
					color: #8F9399;
					font-size: 12px;
				}
			}
		}
	}
</style>
